import { Component, OnInit , DoCheck} from '@angular/core';
import { College } from '../entity';
import {SchoolService} from '../school.service';

@Component({
  selector: 'app-college',
  templateUrl: './college.component.html',
  styleUrls: ['./college.component.css']
})
export class CollegeComponent implements OnInit, DoCheck {
  private n: number;
  private colleges: College[];
  private dataShow: College[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private schoolService: SchoolService) { }

  ngOnInit() {
    this.getColleges();
    // this.leng = this.departments.length;
  }
  // DoCheck 触发变更检测机制就是调用DoCheck钩子，目的是保证组件属性和浏览器的显示同步
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.colleges[startNum] !== null) {
          this.dataShow.push(this.colleges[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getColleges(): void {
    this.schoolService.getColleges ()
      .subscribe(res => this.colleges = res);
  }
  delete(college: College | number): void {
    this.colleges = this.colleges.filter(h => h !== college);
    this.schoolService.deleteCollege(college).subscribe(res => alert(res.message));
  }
}
