import {Component, DoCheck, OnInit} from '@angular/core';
import {Teacher, Title} from '../entity';
import { TitleService } from '../title.service';

@Component({
  selector: 'app-title',
  templateUrl: './title.component.html',
  styleUrls: ['./title.component.css']
})
export class TitleComponent implements OnInit, DoCheck{
  private titles: Title[];
  private n: number;
  private dataShow: Title[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private titleService: TitleService) { }

  ngOnInit() {
    this.getTitles();
  }

  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.titles[startNum] !== null) {
          this.dataShow.push(this.titles[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getTitles(): void {
    this.titleService.getTitles()
      .subscribe(res => this.titles = res);
  }
  delete(title: Title | number): void {
    this.titles = this.titles.filter(h => h !== title);
    this.titleService.deleteTitle(title).subscribe(res => alert(res.message));
  }
}
